import firebase from 'firebase';
import { firestoreRef } from './components/config/firebaseRef';
import { setCookie, getCookie } from './services/CommonServices';
import {getLocalStorage} from './services/CommonServices';

export const initializeFirebase = () => {
    if (!firebase.apps.length) {
        try {
            firebase.initializeApp({
                messagingSenderId: '273870685964' // troque pelo seu sender id 
            });
        } catch (err) {
            console.error('Firebase initialization error raised', err.stack);
        }
    }
}

export const askForPermissioToReceiveNotifications = async () => {
    try {

        const messaging = firebase.messaging();

        await messaging.requestPermission();
        const usertoken = await messaging.getToken();
        const useremailID = getLocalStorage("sapeRidesEmailId");
        const userrouteID = getLocalStorage("sapeRidesUserRouteId");
        console.log('hi',useremailID);
        if (usertoken) {
            var docid = "checkdoc";
            firestoreRef.collection("fcmnotifications").where("emailID", "==", useremailID).get()
                .then(function (querySnapshot) {
                    console.log("Insideeee...", querySnapshot)
                    
                    querySnapshot.forEach(function (doc) {
                        docid = doc.id;
                        console.log('doc.id', doc.id);
                        firestoreRef.collection("fcmnotifications").doc(doc.id).set({
                            emailID: useremailID,
                            token: usertoken,
                            routeId: userrouteID

                        }, { merge: true });
                        
                    });
                    firestoreRef.collection("fcmnotifications").doc(docid).get()
                        .then(doc => {
                            if (!doc.exists) {
                                console.log('No such document!');
                                firestoreRef.collection("fcmnotifications").add({
                                    emailID: useremailID,
                                    token: usertoken,
                                    routeId: userrouteID
                                })
                                    .then(function (docRef) {
                                        console.log("Document written with ID: ", docRef.id);
                                    })
                                    .catch(function (error) {
                                        console.error("Error adding document: ", error);
                                    });


                            } else {
                                console.log('Document data:', doc.data());
                            }
                        })
                        .catch(err => {
                            console.log('Error getting document', err);
                        });

                 

                })
                .catch(function (error) {
                    console.log("Error getting documents: ", error);
                });



        }
        console.log('user token: ', usertoken);

        return usertoken;
    } catch (error) {
        console.error(error);
    }
}