import React, { Component } from 'react';
import {AdminAppBar} from '../../SapeRidesAppBar'
import LiveTracking from '../../Livetracking'
import baseUrl from '../../../config';
import { askForPermissioToReceiveNotifications } from '../../../push-notifications';

import AdminOngoingTrip from '../../OngoingTripsCard/AdminOngoingTrip'

class AdminDashboard extends Component {
  constructor(props) {
    super(props)
    askForPermissioToReceiveNotifications()
    this.state = {
      title: '',
      msgbody: '',
      routeId: ''
    }
  }

  clickSendNotification = () => {
    console.log('clickSendNotification')
    fetch(`${baseUrl}/routeusers/notify`, {
      method: "POST",
      body: JSON.stringify({"title": this.state.title,"msgbody": this.state.msgbody,"routeId": this.state.routeId}),
      headers: {
        "Content-Type": "application/json"
      }
    }).then(function(response) {
      console.log('first promise resolved')
      console.log(response)
      return response.json()
    }).then(res => {
      console.log('second promise resolved')
      console.log(res);

    })
    // this.props.history.push(`/registration`)
  }

  handleChange(event) {
    this.setState({title: event.target.value})
  }
  handleBodyChange(event) {
    this.setState({msgbody: event.target.value})
  }
  handleRouteChange(event) {
    this.setState({routeId: event.target.value})
  }

  changeView = (redirectUrl) => {
    console.log('parent props history')
    console.log(this.props.history)
    console.log(redirectUrl)
    this.props.history.push(redirectUrl)
  }

  onlogout = () => {
    this.props.history.push('/');
    window.location.reload();
  }

  render() {
    const { classes } = this.props;
    return (
      <React.Fragment>
        <AdminAppBar changeView={this.changeView} onlogout={this.onlogout}/>
       
        <AdminOngoingTrip changeView={this.changeView}/>
      </React.Fragment>
    );
  }
}

export default AdminDashboard;
