import React, { Component } from 'react';
import { withStyles } from '@material-ui/core/styles';
import Card from '@material-ui/core/Card';
import CardActions from '@material-ui/core/CardActions';
import CardContent from '@material-ui/core/CardContent';
import Button from '@material-ui/core/Button';
import Typography from '@material-ui/core/Typography';
import IconButton from '@material-ui/core/IconButton';
import MyLocationIcon from '@material-ui/icons/MyLocation';
import ShareIcon from '@material-ui/icons/Share';
import { Link } from 'react-router-dom'
import classNames from 'classnames';
import Icon from '@material-ui/core/Icon';
import CallIcon from '@material-ui/icons/Phone';

const styles = {
  card: {
    marginBottom: "10px",
    textAlign: "left",
    position: 'relative',
  },
  bullet: {
    display: 'inline-block',
    margin: '0 2px',
    transform: 'scale(0.8)',
  },
  title: {
    fontSize: 14,
  },
  pos: {
    marginBottom: 12,
  },
  cssRoot: {
    backgroundColor: "#c5072a"
  },
  moveLeft: {
    display: "flex",
    justifyContent: "flex-end"
  },
  qrcodeColor: {
    color: 'blank'
  }
};

class OngoingTripsCard extends Component {

  render() {
    const { classes, boarded, updateToScanState, callDriverActionClick, callSOSActionClick } = this.props;
    return (
     <React.Fragment>
       <Card className={classes.card}>
        <CardContent>
          <Typography className={classes.title} color="textSecondary" gutterBottom>
            {boarded? 'Ongoing Trip' :  'Trip started'}
          </Typography>
          <Typography variant="h5" component="h2">
            Your 8AM trip has started
          </Typography>
          <Typography className={classes.pos} color="textSecondary">
           { boarded? 'You are on the trip' : 'Cab is 20 mins away'}
          </Typography>
          {boarded &&
            <React.Fragment>
              <Button variant="contained" color="primary" size="small"
              className={`end-trip-btn ${classes.cssRoot}`}>End Trip</Button>
            </React.Fragment>}
        </CardContent>
        <CardActions className={classes.moveLeft}>
            <IconButton aria-label="Call Driver" alt="Call Driver" onClick={() => {callDriverActionClick('+91-9535238692')}}>
              <CallIcon/>
            </IconButton>
          <Link to="/dashboard/livetracking">
            <IconButton aria-label="Add to favorites">
              <MyLocationIcon />
            </IconButton>
          </Link>
          {boarded &&
            <React.Fragment>
              <IconButton aria-label="Share" className={classes.qrcodeColor} onClick={() => {updateToScanState(true)}}>
                <ShareIcon/>
              </IconButton>
              <IconButton>
                <i className="far fa-comment-dots"></i>
              </IconButton>
              <IconButton color="secondary">
                <i className="fas fa-broadcast-tower" aria-hidden="true"></i>
              </IconButton>
            </React.Fragment>}
          {!boarded &&
            <React.Fragment>
              <IconButton aria-label="Share" className={classes.qrcodeColor} onClick={() => {updateToScanState(true)}}>
                <i class="fas fa-qrcode"></i>
              </IconButton>
              <Button
              variant="contained"
              color="primary"
              size="small"
              className={classes.cssRoot}
            >
              Not Coming
            </Button>
            </React.Fragment>}
        </CardActions>
        <img className="sosCallIcon" src="/assests/sos-call.jpg" width="100" height="100" onClick={() => {callSOSActionClick()}}></img>
      </Card>
     </React.Fragment>
    );
  }
}

export default withStyles(styles)(OngoingTripsCard);
                