import React, { Component } from 'react';
import { BrowserRouter as Router, Route, Link } from "react-router-dom";
import './Login.css';
import baseUrl from '../../config'

import { withStyles } from '@material-ui/core/styles';
import LockIcon from '@material-ui/icons/Lock';
import AccountCircleIcon from '@material-ui/icons/AccountCircle';
import TextField from '@material-ui/core/TextField';

// import { withNavigation } from 'react-navigation';


import {setLocalStorage, getLocalStorage} from '../../services/CommonServices'
import Button from '@material-ui/core/Button';

const styles = theme => ({
  container: {
    display: 'flex',
    flexWrap: 'wrap',
  },
  textField: {
    marginLeft: theme.spacing.unit,
    marginRight: theme.spacing.unit,
  },
  dense: {
    marginTop: 16,
  },
  menu: {
    width: 200,
  },
});


class Login extends Component {
  
  constructor(props) {
    super(props)
    const userId = getLocalStorage("sapeRidesUserId");
    if(userId) {
      this.props.history.push(`/dashboard`)
    }
    this.state = {
      name: ''
    }
  }

  onLogin = () => {
    console.log('onLogin')
    console.log(this.state.name);
    fetch(`${baseUrl}/users/authenticate`, {
      method: "POST",
      body: JSON.stringify({"username": this.state.name}),
      headers: {
        "Content-Type": "application/json"
      }
    }).then(function(response) {
      console.log('first promise resolved')
      console.log(response)
      return response.json()
    }).then(res => {
      console.log('second promise resolved')
      console.log(res)
      if(res) {
        setLocalStorage("sapeRidesToken", `${res.token}`)
        setLocalStorage("sapeRidesUserId", `${res.user.id}`)
        setLocalStorage("sapeRidesUserGroup", `${res.user.userGroup}`)
        setLocalStorage("sapeRidesUserRouteId", `${res.user.routeId}`)
        setLocalStorage("sapeRidesEmailId", `${res.user.emailID}`)
        setLocalStorage("sapeRidesUserData", JSON.stringify(res.user))
        /*
              // if( notRegisterd) need to add marker
              // will be decide after what new user schema is
              // When new user is entered will we populate 
              // PhoneNo from people portal 
              // and set a boolen not registred
              // Sounds like a doable approch will key in on it
        */

       this.props.history.push(`/dashboard`)
      }
    })

    // this.props.history.push(`/registration`)
  }

  handleChange = name => event => {
    this.setState({
      [name]: event.target.value,
    });
  };

  render() {
    const { classes } = this.props;
    return (
      <div className="body">
        <div className="loginBox">
          <div className="textBoxBackground">
            <img src="/assests/logo.png" width="200px" height="150px"/>
            <div>
              {/* <Icon><LockIcon/></Icon> */}
              <div className='icon-div'>
                <AccountCircleIcon/>
              </div> 
              <input 
              className="inputBox userId"
              placeholder="Enter Employee ID"
              
              value={this.state.name}
              onChange={this.handleChange('name')}>
              </input>
            </div>
            <div className='icon-div'>
              <LockIcon/>
            </div>            
            <input 
              className="inputBox password" 
              placeholder="Enter PIN" 
              type="password">
            </input>
            {/* <button className="loginButton">
              LOGIN
            </button><br/> */}
            <Button className="matrialLoginButton login-cta" onClick={this.onLogin}>Login</Button>
          </div>
        </div>
      </div>
    );
  }
}

export default (withStyles(styles)(Login));
