import React from "react"
import { compose, withProps } from "recompose"
import { withScriptjs, withGoogleMap, GoogleMap, Marker, Polyline } from "react-google-maps";
import { pathCoordinate, stopsCoordinate, adminCoordinat } from './trip.data';
import { getTrips } from '../../services/trips'

const MyMapComponent = compose(
  withProps({
    googleMapURL: "https://maps.googleapis.com/maps/api/js?key=AIzaSyBUdVzFghfUvQ7JCjRDS6NtEzbftZLk5Z0&v=3.exp&libraries=geometry,drawing,places",
   loadingElement: <div style={{ height: `100%` }} />,
    containerElement: <div style={{ height: `400px` }} />,
    mapElement: <div style={{ height: `100%` }} />,
  }),
  withScriptjs,
  withGoogleMap
)((props) =>

  <GoogleMap
    defaultZoom={13}
    defaultCenter={props.center}
  >
  <Polyline path={props.path} 
  options={{ strokeColor: '#FF0000', strokeOpacity: "1.0" }} >
  </Polyline>

  {props.userType === 'adminOld' && 
   props.trips.map((trip, i) => 
    
    ( trip.currentLocation && <Marker key={i} icon={{
      url: "https://image.flaticon.com/icons/png/128/15/15923.png",
      scaledSize: new window.google.maps.Size(16, 16)
    }} position={{ lat: trip.currentLocation.latitude ,lng: trip.currentLocation.longitude}}/>
    )
  )}

{props.userType === 'vendor' && 
   props.stops.map((position, i) => 
    
    <Marker key={i} icon={{
      url: "https://upload.wikimedia.org/wikipedia/commons/thumb/d/d8/Person_icon_BLACK-01.svg/124px-Person_icon_BLACK-01.svg.png",
      scaledSize: new window.google.maps.Size(16, 16)
    }} position={position}/>
    
  )}

{props.userType === 'admin' && 
   props.trips.map((position, i) => 
    
    <Marker key={i} icon={{
      url: "https://image.flaticon.com/icons/png/128/15/15923.png",
      scaledSize: new window.google.maps.Size(16, 16)
    }} position={position}/>
    
  )}

  <Marker key='route' icon={{
            url: "https://image.flaticon.com/icons/png/128/15/15923.png",
            scaledSize: new window.google.maps.Size(16, 16)
        }} position={props.latestPosition} />
    {props.isMarkerShown && props.userIconShow &&
    <Marker icon={ {url: "https://upload.wikimedia.org/wikipedia/commons/thumb/d/d8/Person_icon_BLACK-01.svg/124px-Person_icon_BLACK-01.svg.png",
    scaledSize: new window.google.maps.Size(20, 20)   } }
     position={props.center} onClick={props.onMarkerClick} />}
     
  </GoogleMap>
);

export default class LiveMapComponent extends React.PureComponent {
  constructor(props) {
      super(props);
      this.state = {
      isMarkerShown: false,
      latestPosition: '',
      currentLocation: {lat: 12.979523, lng: 77.695349},
      pathCoordinate:  [],
      userType:props.userType || 'user',
      geoLocationObj: '',
      trips: adminCoordinat,
      tripStops: [],
      stopsCoordinate: stopsCoordinate
      }
      this.showPosition= this.showPosition.bind(this);
      this.locWatchError = this.locWatchError.bind(this);
      this.locWatchSuccess = this.locWatchSuccess.bind(this);
      console.log('state',this.state);
  }

  loadAllTripsData(){
    getTrips().then((snapshot) => {
      snapshot.docs.map( doc => {
        this.setState({ trips: [...this.state.trips, doc.data()] });
      })
     // console.log('teststate', this.state);
      this.updatetrips();
    });


  }

  updatetrips(){
   // this.setState({ trips: []});
    setInterval(() => {
      this.setState({ trips: []});
     
      this.setState({ trips: adminCoordinat });
     // }, 500);
     // this.loadAllTripsData();
    }, 800)
  }

  componentDidMount() {
   this.getLocation();
   if(this.state.userType === 'user'){
    // navigator.geolocation.clearWatch(this.state.geoLocationObj);
    this.delayedShowMarker();
    this.drawTrips(pathCoordinate);
   } else if (this.state.userType === 'vendor') {
    // watch location and getting coordinate if it is driver.
    this.watchLocation();
   } else {
    // this.loadAllTripsData();
    this.updatetrips();
   }

  }
  

  getLocation() {
    if (navigator.geolocation) {
      navigator.geolocation.getCurrentPosition(this.showPosition);
    } else {
    //  this.setState({currentLocation: { props.center }});
    }
  }

  watchLocation(){
    if (navigator.geolocation) {
    const options = {
      enableHighAccuracy: true,
      timeout: 1000,
      maximumAge: 0
    };
    this.drawTrips(pathCoordinate);
    //this.setState({geoLocationObj: navigator.geolocation.watchPosition(this.locWatchSuccess, this.locWatchError, options)});
  //  this.state.geoLocationObj = navigator.geolocation.watchPosition(this.locWatchSuccess, this.locWatchError, options);
    } else {
      console.log('geoLocation is not supported');
    }
  }

  locWatchSuccess(pos){
    const crd = pos.coords;

  if (crd.latitude && crd.longitude) {
    const locVal = { lat: crd.latitude ,lng: crd.longitude};
    this.setState({ latestPosition: locVal, pathCoordinate: [...this.state.pathCoordinate, locVal]})
   // navigator.geolocation.clearWatch(id);
  }

  }

  locWatchError(err){
    console.warn('ERROR(' + err.code + '): ' + err.message);
    this.delayedShowMarker();
  }


  
  showPosition(position) {
      const currentLocation = { lat: position.coords.latitude ,lng: position.coords.longitude};
      this.setState({currentLocation: currentLocation});
     // console.log(this.state);
      return currentLocation;
  }

  drawTrips = (coordinates) => {
    let offset = 0;
    coordinates.forEach((i) => {
        setTimeout(() => {
           this.setState({latestPosition: i, pathCoordinate: [...this.state.pathCoordinate, i]})
        },1000 + offset)
        offset += 1000;
    });
  }

 

  delayedShowMarker = () => {
    
    setTimeout(() => {
      this.setState({ isMarkerShown: true })
    }, 1000)
  }

  handleMarkerClick = () => {
    this.setState({ isMarkerShown: false })
    this.delayedShowMarker()
  }

  render() {
    return (
      <React.Fragment>
        <MyMapComponent
          center={this.state.currentLocation} 
          userIconShow = {this.props.userIconShow || true}
          isMarkerShown={this.state.isMarkerShown}
          onMarkerClick={this.handleMarkerClick}
          path={this.state.pathCoordinate}
          latestPosition={this.state.latestPosition}
          trips={this.state.trips}
          userType={this.state.userType}
          isOpen='true'
          stops={this.state.stopsCoordinate}
        
        />
      </React.Fragment>
    )
  }
}
