import React, { Component, Fragment } from 'react';
import { BrowserRouter as Router, Route, Link } from "react-router-dom";

import {getLocalStorage} from '../../services/CommonServices'
// import { BrowserRouter as Router, Route, Link } from "react-router-dom";
import UserDashboardWrapper from './UserDashBoard'
import AdminDashboard from './AdminDashboard'
import VendorDashboard from './VendorDashboard'
import UserAppBar from '../SapeRidesAppBar'
import Registration from '../Registration'
import LiveTracking from '../Livetracking'
import BroadcastNotification from '../BroadcastNotification'

class Dashboards extends Component {

  constructor(props) {
    super(props)
    this.state = {
     userId: getLocalStorage("sapeRidesUserId"),
     userGroup: getLocalStorage("sapeRidesUserGroup")
    }
    console.log('state')
    console.log(this.state)
  }
  changeView = (redirectUrl) => {
    console.log('props history')
    console.log(this.props.history)
    console.log(redirectUrl)
    this.props.history.push(redirectUrl)
  }
  render() {
    const { classes } = this.props;
    const { userGroup } = this.state

    // console.log('render')
    // console.log(userGroup)
    console.log('path match')
    console.log(this.props.match.path)
    console.log(this.props.match.url)
    
    return (
     <Router>

      <Fragment>
            {userGroup === 'admin' &&
              <div>
                <Route exact path={this.props.match.path} component={AdminDashboard} />
                <Route exact path={`${this.props.match.path}/livetracking`} render={() => <LiveTracking userType="admin" noAppBar={true}/>}/>
                <Route exact path={`${this.props.match.path}/livetracking/id`} render={() => <LiveTracking userType="user" userIconShow={false} noAppBar={true}/>}/>
                <Route exact path={`${this.props.match.path}/sendNotification`} render={() => {return (<BroadcastNotification/>)}}/>
                
              </div>
            }
            {userGroup === 'signee' &&
            <Fragment>
              <div>
                <Route exact path={this.props.match.path} component={UserDashboardWrapper} />
                <Route exact path={`${this.props.match.path}/livetracking`} render={() => <LiveTracking userType="user"/>}/>
                <Route exact path={`${this.props.match.path}/updateprofile`} component={Registration} />
              </div>
            </Fragment>
            }
            {userGroup === 'vendor' &&
              <div>
              <Route exact path={this.props.match.path} component={VendorDashboard} />
              <Route exact path={`${this.props.match.path}/sendNotification`} render={() => {return (<div>FOO</div>)}}/>
              <Route exact path={`${this.props.match.path}/livetracking`} render={() => <LiveTracking userType="vendor"/>}/>
              </div>
            }
          </Fragment>
     </Router>
    );
  }
}

export default Dashboards;
                

