let siteLocalStorageItems = []

export function setLocalStorage(keyName, valueName) {
  siteLocalStorageItems.push(keyName)
  localStorage.setItem(keyName, valueName);
}

export function getLocalStorage(keyName) {
  return localStorage.getItem(keyName);
}

export function deleteLocalStorage(keyName) {
  console.log('deleteLocalStorage', keyName)
  localStorage.removeItem(keyName);
}

export function deleteAllLocalStorage() {
  siteLocalStorageItems = siteLocalStorageItems.length === 0? ['sapeRidesToken', 'sapeRidesUserData', 'sapeRidesUserGroup', 'sapeRidesUserId', 'sapeRidesUserRouteId', 'sapeRidesUserData'] : siteLocalStorageItems
  siteLocalStorageItems.forEach(key => deleteLocalStorage(key))
  siteLocalStorageItems = []
}





