import React, { Component, Fragment } from 'react';
import { BrowserRouter as Router, Route, Link } from "react-router-dom";


import { withStyles } from '@material-ui/core/styles';
import AppBar from '@material-ui/core/AppBar';
import Toolbar from '@material-ui/core/Toolbar';
import Typography from '@material-ui/core/Typography';
import Button from '@material-ui/core/Button';
import IconButton from '@material-ui/core/IconButton';
import MenuIcon from '@material-ui/icons/Menu';
import MenuItem from '@material-ui/core/MenuItem';
import Menu from '@material-ui/core/Menu';
import Badge from '@material-ui/core/Badge';
import AccountCircle from '@material-ui/icons/AccountCircle';
import ContactPhone from '@material-ui/icons/ContactPhone';
import NotificationsIcon from '@material-ui/icons/Notifications';

import {deleteAllLocalStorage} from '../../services/CommonServices'

import './index.css'

class SapeRidesAppBar extends Component {

  state = {
    anchorEl: null
  }

  handleMenu = event => {
    this.setState({ anchorEl: event.currentTarget });
  };
  
  callAdmin = event => {
    window.open('tel:+91-9535238692');
    console.log('Call Admin');
  };

  handleClose = () => {
    this.setState({ anchorEl: null });
  };

  navigateToUpdateProfile = () => {
    this.handleClose()
    // this.props.history.push(this.props.redirectUrl)
    this.props.changeView(this.props.redirectUrl);
  }

  navigateToBradcastNotification = () => {
    this.handleClose()
    this.props.changeView('/dashboard/sendNotification')
  }

  checkIfCanNav = () => {
    console.log('checkIfCanNav')
    deleteAllLocalStorage()
    console.log('this.props.history')
    console.log(this.props.history)
    // this.props.history.push('/');
    this.props.onlogout()
  }

  render() {
    const { classes, isUpdateProfile, sendNotification } = this.props;
    const { anchorEl } = this.state;
    const open = Boolean(anchorEl);
    console.log('SapeRidesAppBar render');
    return (
      <AppBar position="static" color="default" className="flexAppBar">
        <div className="flexAppBar">
          <Toolbar>
            <Typography variant="h6" color="inherit">
              SapeRides
            </Typography>
          </Toolbar>
          <Fragment>
          <div className="header-right-menu">
            <IconButton onClick={this.callAdmin} color="inherit">
              <ContactPhone />
            </IconButton>
            <IconButton color="inherit">
              <Badge badgeContent={6} color="secondary">
                <NotificationsIcon />
              </Badge>
            </IconButton>
            <IconButton
              aria-owns={open ? 'menu-appbar' : undefined}
              aria-haspopup="true"
              onClick={this.handleMenu}
              color="inherit"
            >
              <AccountCircle />
            </IconButton>
            <Menu
              id="menu-appbar"
              anchorEl={anchorEl}
              anchorOrigin={{
                vertical: 'top',
                horizontal: 'right',
              }}
              transformOrigin={{
                vertical: 'top',
                horizontal: 'right',
              }}
              open={open}
              onClose={this.handleClose}
            >
              {isUpdateProfile && <MenuItem onClick={this.navigateToUpdateProfile}>Update Profile</MenuItem>}
              {sendNotification && <MenuItem onClick={this.navigateToBradcastNotification}>Send notification</MenuItem>}
              <MenuItem onClick={this.checkIfCanNav}>Logout</MenuItem>
            </Menu>
          </div>
          </Fragment>
          </div>
        </AppBar>
    );
  }
}

export class UserAppBar extends Component {
  render() {
    return (<SapeRidesAppBar isUpdateProfile={true} {...this.props}/>)
  }
}

export class AdminAppBar extends Component {
  render() {
    return (<SapeRidesAppBar isUpdateProfile={false} sendNotification={true} {...this.props}/>)
  }
}

export class VendorAppBar extends Component {
  render() {
    return (<SapeRidesAppBar isUpdateProfile={false} {...this.props}/>)
  }
}

