import React, { Component } from 'react';
import { firestoreRef } from '../../config/firebaseRef'
import Paper from '@material-ui/core/Paper';
import './index.css';
import ReactDOM from 'react-dom';
import { isNull } from 'util';
import * as firebase from "firebase";
import { Link } from 'react-router-dom'
import Button from '@material-ui/core/Button';
import {VendorAppBar} from '../../SapeRidesAppBar'
import moment from 'moment'
const uuid = require('uuidv4');



class VendorDashboard extends Component {
  
  constructor(props) {
    super(props);
    const driverId = localStorage.getItem("sapeRidesUserId");    
    this.state = {
      driverId : driverId,
      nextTripDetails: null,
      driverDetails: null,
      routeDetails: null,
      startTripString: null,
      tripDetails: null
    };
  }

  componentDidMount() {
    this.getUserDetails()
  }

  getUserDetails = () => {
    const { driverId } = this.state
    firestoreRef.collection('userDataV3')
      .where("id", "==", driverId)
      .get()
      .then((querySnapshot) => {
        querySnapshot.forEach((doc) => {
          const data = doc.data();
          console.log("data111111")
          console.log(data)
          this.setState({driverDetails: data})
          this.getRouteDetails(data.routeId, () => {
            console.log('cb')
            console.log(data.ongoingTrip)
            console.log(data.ongoingTrip? 'dcs' : 'dsfcd');
            if(data.ongoingTrip && data.ongoingTrip != 'null') {
              console.log('in if')
              this.getTripDetails(data.ongoingTrip)
            } else{
              console.log("calling setStartTripData")
            this.setStartTripData()}
          })
          
          // else get ongoing trip details to show driver
        });
      })
  }

  getTripDetails = (tripId, cb) => {
    firestoreRef.collection('tripsDataV3')
      .where("id", "==", tripId)
      .get()
      .then((querySnapshot) => {
        querySnapshot.forEach((doc) => {
          const data = doc.data();
          console.log("data22222")
          console.log(data)
          this.setState({tripDetails: data, isTripGoing: true})
          // this.setStartTripData()
        });
      })
  } 

  getRouteDetails = (routeId, cb) => {
    console.log('routeId', routeId)
    firestoreRef.collection('RouteDataV3')
      .where("id", "==", routeId)
      .get()
      .then((querySnapshot) => {
        querySnapshot.forEach((doc) => {
          const data = doc.data();
          console.log("data3333")
          console.log(data)
          this.setState({routeDetails: data})
          console.log(cb)
          if(cb) {
            console.log('calling call back')
            cb()
          }
        });
      })
  }

  setStartTripData = () => {
    const { routeDetails } = this.state;
    console.log('setStartTripData4444');
    const nowTime = moment().format('h:mm:ss a')
    const {pickUpTrip, dropTrip } = routeDetails
    var beginningTime = moment(pickUpTrip.estimatedStartTime);
    console.log(beginningTime.isBefore(nowTime))
    if(beginningTime.isBefore(nowTime)) {
      // start pick up
      const tripType = 'pickUpTrip'
      this.setState({
        startTripString: `start pickup trip for ${routeDetails.routeNumber}`,
        isTripGoing: false,
        tripType: tripType,
        startTime: nowTime
      })
    } else {
      //start drop
      const tripType = 'dropTrip'
      this.setState({
        startTripString: `start drop trip for ${routeDetails.routeNumber}`,
        isTripGoing: false,
        tripType: tripType,
        startTime: nowTime
      })
    }
  }

  onlogout = () => {
    this.props.history.push('/');
    window.location.reload();
  }

  postNewDocumentID = (isTripGoing) => {
    const { driverId, routeDetails, tripType, startTime } = this.state
    console.log(driverId)
    console.log(routeDetails)
    this.setState({
      isTripGoing: true
    })
    const id = uuid();
    console.log(isTripGoing);
    var boardedArray=[];
    var routeId=localStorage.getItem("sapeRidesUserRouteId");
    const toAddData =  {id: id,
      isOngoingTrip: true,
      routeID: routeId,
      tripId: id,
      boardedUsers:boardedArray,
      deboardedUsers:boardedArray,
      currentLocation:new firebase.firestore.GeoPoint(0,0),
      geoLocationsArray:boardedArray,
      date: new Date(),
      tripType: tripType,
      expectedTripTime : routeDetails[tripType],
      ActualTripTime : {
        actualStartTime: startTime
      }
    };
      this.setState({tripDetails: toAddData})
    firestoreRef.collection("tripsDataV3").doc(id).set(toAddData)
    firestoreRef.collection("userDataV3").doc(driverId).set({
      ongoingTrip: id
    }, { merge: true })

    routeDetails.users.forEach(userId => {
      firestoreRef.collection("userDataV3").doc(userId).set({
        ongoingTrip: id,
        isOngoingTrip: true
      }, { merge: true })
    })


    firestoreRef.collection("RouteDataV3").doc(routeDetails.id).set({
      ongoingTrip: id
    }, { merge: true })
    firestoreRef.collection("userDataV3").doc(driverId)
      .onSnapshot( (doc) => {
        console.log('updated data')
        console.log(doc.data())
      })
  }

  navigateToLiveLoc = () => {
    this.props.history.push('dashboard/livetracking')
  }

  endTrip = () => {
    this.setStartTripData()
    const {tripDetails, driverId, routeDetails} = this.state
    this.setState({isTripGoing: false})
    console.log('EndTrip')
    console.log(tripDetails, driverId, routeDetails)
    firestoreRef.collection("tripsDataV3").doc(tripDetails.id).set({
      ongoingTrip: null,
      isOngoingTrip: false
      // ActualTripTime : {
      //   actualStartTime: this.state.startTime,
      //   actualEndTime: moment().format('h:mm:ss a')
      // }
    }, { merge: true })
    firestoreRef.collection("userDataV3").doc(driverId).set({
      ongoingTrip: null
    }, { merge: true })
    firestoreRef.collection("RouteDataV3").doc(routeDetails.id).set({
      ongoingTrip: null
    }, { merge: true })
    routeDetails.users.forEach(userId => {
      firestoreRef.collection("userDataV3").doc(userId).set({
        ongoingTrip: null,
        isOngoingTrip: false
      }, { merge: true })
    })
  }

  render() {
    console.log(this.state.isTripGoing)
    return (
    <React.Fragment>
      <VendorAppBar onlogout={this.onlogout}/>
      <div className="wrapper">
        { !this.state.isTripGoing ?
          <Paper className={`screen-list ${this.state.tripStatus ? 'hide' : ''} `}>
            <h1 className="tips-title">
              {this.state.startTripString?
                this.state.startTripString
                :'Checking for you next trip please wait'}
            </h1>
            <div className="action-tracker">
                <Button class="btn" onClick={this.postNewDocumentID}>Go</Button>
            </div>
          </Paper> :
          <div className={` ${this.state.tripStatus ? 'hide' : ''} `}>
            <Paper className="screen-list ">
              <h1 className="tips-title">You have one ongoing Trip</h1>
              <div className="action-tracker">
                  <Button onClick={this.navigateToLiveLoc}>Track Now</Button>
              </div>
            </Paper>
            <Paper className="screen-list ">
              <h1 className="tips-title">End the Trip</h1>
              <div className="action-tracker">
                  <Button className="action-btn"  onClick={this.endTrip}>End</Button>
              </div>
            </Paper>
          </div>
        }
      </div>
      </React.Fragment>
    );
  }
}

export default VendorDashboard;
                