import React, { Component } from 'react';
import { BrowserRouter as Router, Route, Link } from "react-router-dom";
import OngoingTripsCard from '../../OngoingTripsCard'
import UpcomingTripsCard from '../../UpcomingTripsCard'
import RecentTripsCard from '../../RecentTripsCard'
import {UserAppBar} from '../../SapeRidesAppBar'
import './index.css';

class UserDashboard extends Component {
  constructor(props) {
    super(props);
    this.state = {
      userStatus: null,
      upcomingTrip:'',
      recentTrip: '',
      boarded: false
    };

    // this.getUserData();  
  }

  componentWillMount() {
    const monthNames = ["Jan", "Feb", "Mar", "Apr", "May", "Jun",
      "Jul", "Aug", "Sep", "Oct", "Nov", "Dec"
    ];
    const days = ['Sun', 'Mon', 'Tue', 'Wed', 'Thu', 'Fri', 'Sat'];

    Date.prototype.today = function () { 
        return ((this.getDate() < 10)?"0":"") + this.getDate() +"/"+(((this.getMonth()+1) < 10)?"0":"") + (this.getMonth()+1) +"/"+ this.getFullYear();
    }
    Date.prototype.yesterday = function () { 
      return ( (((this.getDate() < 10)?"0":"") + this.getDate()) - 1) +"/"+(((this.getMonth()+1) < 10)?"0":"") + (this.getMonth()+1) +"/"+ this.getFullYear();
    }
    Date.prototype.tomorrow = function () { 
      return ( parseInt( ((this.getDate() < 10)?"0":"") + this.getDate()) + 1) +"/"+(((this.getMonth()+1) < 10)?"0":"") + (this.getMonth()+1) +"/"+ this.getFullYear();
    }
    var newDate = new Date();
    var todayArr = newDate.today().split('/');
    var todayMorningTime = new Date(todayArr[2], todayArr[1] - 1, todayArr[0], 8, 0, 0 ,0);
    var todayEveningTime = new Date(todayArr[2], todayArr[1] - 1, todayArr[0], 17, 0, 0 ,0);
    var currTime = new Date();
    var sDayArr = newDate.yesterday().split('/');
    var sDayDate = new Date(sDayArr[2], sDayArr[1] - 1, sDayArr[0], 0, 0, 0 ,0);
    var tmrwArr = newDate.tomorrow().split('/');
    var tmrwDate = new Date(tmrwArr[2], tmrwArr[1] - 1, tmrwArr[0], 0, 0, 0 ,0);
    var todayDateFormat = days[newDate.getDay()] + ' ' + monthNames[newDate.getMonth()] + ' ' + newDate.getDate();
    var yesterdayFormat = days[sDayDate.getDay()] + ' ' + monthNames[sDayDate.getMonth()] + ' ' + sDayDate.getDate();
    var tmorrowFormat = days[tmrwDate.getDay()] + ' ' + monthNames[tmrwDate.getMonth()] + ' ' + tmrwDate.getDate();
    if (todayMorningTime < currTime && todayEveningTime > currTime) {
      const upcomingVal = { title: 'Today', time: '5.00 PM', noon: 'Evening', date: todayDateFormat};
      const recentVal = { title: 'Today', time: '8.00 AM', noon: 'Morning', date: todayDateFormat};
      this.setState({ upcomingTrip: upcomingVal,  recentTrip: recentVal});
    } else if (todayEveningTime < currTime) {
      const upcomingVal = { title: 'Tomorrow', time: '8.00 AM', noon: 'Morning', date: tmorrowFormat};
      const recentVal = { title: 'Today', time: '5.00 PM', noon: 'Evening', date: todayDateFormat};
      this.setState({ upcomingTrip: upcomingVal,  recentTrip: recentVal});
    } else if (todayMorningTime > currTime) {
      const upcomingVal = { title: 'Today', time: '8.00 AM', noon: 'Morning', date: todayDateFormat};
      const recentVal = { title: 'Yesterday', time: '5.00 PM', noon: 'Evening', date: yesterdayFormat};
      this.setState({ upcomingTrip: upcomingVal,  recentTrip: recentVal});
    }
  }

  changeView = (redirectUrl) => {
    this.props.changeView('/dashboard/updateprofile')
  }

  onlogout = () => {
    this.props.onLogout('/');
  }

  hideSuccessMsg() {
    document.querySelector('.user-status').classList.remove('show');
    document.querySelector('.user-status').classList.add('hide');
  }

  showSuccessMsg() {
    var queryStringData = this.props.location.search.substr(1);
    if (queryStringData) {
      var userStatus = queryStringData.split('=');
      if (userStatus && userStatus[1] == 'boarded') {
        this.setState({boarded: true})
        // document.querySelector('.user-status').classList.remove('hide');
        // document.querySelector('.user-status').classList.add('show');
        // setTimeout(this.hideSuccessMsg, 5000);
      }
    }
  }

  componentDidMount() {
    // this.showSuccessMsg();
  }
  
  render() {
    const { upcomingTrip, recentTrip } = this.state;
    const { updateToScanState, boarded, callDriverActionClick, callSOSActionClick } = this.props;
    console.log('UserDashboard:', boarded)
    return (
      <React.Fragment>
        <UserAppBar redirectUrl={'/dashboard/updateprofile'} changeView={this.changeView} onlogout={this.onlogout}/>
        <div className="wrapper">
          <OngoingTripsCard boarded={boarded} callSOSActionClick={() => {callSOSActionClick()}} callDriverActionClick={() => {callDriverActionClick()}} updateToScanState={(status) => {updateToScanState(status)}}/>
          <UpcomingTripsCard upcomingTrip={upcomingTrip}/>
          <RecentTripsCard recentTrip={recentTrip}/>
        </div>
        </React.Fragment>
    );
  }
}

export default UserDashboard;
