import React, { Component } from 'react';
import MenuItem from '@material-ui/core/MenuItem';
import TextField from '@material-ui/core/TextField';
import Button from '@material-ui/core/Button';
import {AdminAppBar} from '../SapeRidesAppBar'
import baseUrl from '../../config'

class BroadcastNotification extends Component {

  constructor(props) {
    super(props)
    this.state = {
      title: '',
      msgbody: '',
      routeId: 'b79fad3c-392c-11e9-b210-d663bd873d93'
    }
  }

  clickSendNotification = () => {
    this.setState({
      title: '',
      msgbody: '',
      routeId: 'b79faaa8-392c-11e9-b210-d663bd873d93'
    })
    console.log('clickSendNotification')
    fetch(`${baseUrl}/routeusers/notify`, {
      method: "POST",
      body: JSON.stringify({"title": this.state.title,"msgbody": this.state.msgbody,"routeId": this.state.routeId}),
      headers: {
        "Content-Type": "application/json"
      }
    }).then(function(response) {
      console.log('first promise resolved')
      console.log(response)
      return response.json()
    }).then(res => {
      console.log('second promise resolved')
      console.log(res);

    })
    // this.props.history.push(`/registration`)
  }

  handleChange(event) {
    this.setState({title: event.target.value})
  }
  handleBodyChange(event) {
    this.setState({msgbody: event.target.value})
  }
  handleRouteChange(event) {
    this.setState({routeId: event.target.value})
  }

  changeView = (redirectUrl) => {
    console.log('parent props history')
    console.log(this.props.history)
    console.log(redirectUrl)
    this.props.history.push(redirectUrl)
  }

  onlogout = () => {
    this.props.history.push('/');
    window.location.reload();
  }

  render() {
    const { classes } = this.props;
    return (
     <React.Fragment>
       <AdminAppBar changeView={this.changeView} onlogout={this.onlogout}/>
        <div>Admin Dashboard</div> 
          <TextField
            label="Enter Title"
            type="text"
            margin="normal"
            name="title"
            value={this.state.title}
            onChange={this.handleChange.bind(this)}
          />

          <TextField
            label="Enter Message Body"
            type="text"
            name="msgbody"
            margin="normal"
            value={this.state.msgbody}
            onChange={this.handleBodyChange.bind(this)}
          />

          <TextField
            label="Enter Route Id"
            type="text"
            name="routeId"
            margin="normal"
            value={this.state.routeId}
            onChange={this.handleRouteChange.bind(this)}
          />

          <Button variant="contained" color="secondary" onClick={this.clickSendNotification}>
            Send Notifications
          </Button>
     </React.Fragment>
    );
  }
}

export default BroadcastNotification;
                