import React, { Component } from 'react';
import { BrowserRouter as Router, Route, Link } from "react-router-dom";

import Scanner from '../../Scanner'
import UserDashboard from './UserDashboard'
import { askForPermissioToReceiveNotifications } from '../../../push-notifications';


// import UserAppBar from '../../SapeRidesAppBar'


class UserDashboardWrapper extends Component {

  constructor(props){
    super(props);
    askForPermissioToReceiveNotifications();
    this.state = {
      toScan: false,
      boarded: false
    }
  }

  changeView = (redirectUrl) => {
    console.log('parent props history')
    console.log(this.props.history)
    console.log(redirectUrl)
    this.props.history.push(redirectUrl)
  }

  onLogout = () => {
    this.props.history.push('/')
    window.location.reload();
  }

  updateToScanState = (scanStatus) => {   
    if(scanStatus.boarded) {
      console.log(scanStatus.boarded)
      this.setState({
        boarded: true,
        toScan: false
      })
    }
    else {
      this.setState({
        toScan: scanStatus
      })
    }
  }

  callDriverActionClick = (num) => {
    window.open(`tel:${num}`)
    console.log('Call Driver');
  }

  callSOSActionClick = () => {
    console.log('Call SOS');
  }

  componentDidMount() {
    
  }

  render() {
    const { classes } = this.props;
    const { boarded, toScan } = this.state;
    console.log('boarded:', boarded);
    return (
     <React.Fragment>
        {toScan && <Scanner updateToScanState={this.updateToScanState}/>}
        {!toScan && <UserDashboard boarded={boarded} callSOSActionClick={this.callSOSActionClick} callDriverActionClick={this.callDriverActionClick} updateToScanState={this.updateToScanState} changeView={this.changeView} onLogout={this.onLogout}/>}
     </React.Fragment>
    );
  }
}

export default UserDashboardWrapper;
                